.detailsCheckbox{position:relative}.detailsCheckbox__title{cursor:pointer}.detailsCheckbox__title::before{content:"►"}.open .detailsCheckbox__title::before{content:"▼"}.detailsCheckbox__content{display:none;border-top-width:0;padding:10px}@media screen and (min-width: 1220px){.detailsCheckbox__content{position:absolute;min-width:100%;background:#0d100f;border:1px solid rgba(126,126,126,.4)}}.open .detailsCheckbox__content{display:block}